            // symbol: 'path://M848.122258 164.750679c-4.266594-30.591477-23.167604-60.798961-70.654793-73.556077L453.377001 4.369419c-47.444523-12.714449-78.931985 4.010598-97.875661 28.372848L255.108389 160.441419c-25.258235-3.711937-75.604042 10.70915-88.787817 59.945643l-14.762414 192.722041 35.028734 9.386506-12.245124 45.695219c-19.32767 72.190767 74.878721 96.553017 94.035727 25.21557l12.245124-45.69522 424.824743 113.790056-12.245124 45.737886c-19.071674 71.337448 74.665391 97.321004 93.993061 25.172903l12.245124-45.652553 35.071401 9.34384 83.582572-174.24769c13.269107-49.279158-23.167604-86.910515-46.932532-96.297022l-23.039606-160.807919zM298.115654 333.323799c-29.439497-7.893198-47.316525-36.522043-39.977984-63.998907 7.338541-27.476864 37.204698-43.305927 66.644195-35.412728 29.396831 7.893198 47.273859 36.522043 39.935318 63.998907-7.381207 27.434198-37.162032 43.305927-66.601529 35.412728z m19.754329-171.986395l78.505326-101.800928c10.325157-14.80508 22.484949-24.234253 43.519256-18.815678l334.330289 89.641135c20.820978 5.759902 26.580879 20.010325 28.159519 38.015351l17.066375 127.443156L317.869983 161.337404z m429.68866 292.389671c-29.396831-7.893198-47.316525-36.522043-40.02065-63.998906 7.423873-27.476864 37.162032-43.305927 66.686861-35.412729 29.396831 7.893198 47.316525 36.522043 39.935317 63.998907-7.381207 27.476864-37.204698 43.305927-66.601528 35.412728zM180.272334 599.772581l82.814585 90.835781-227.750776 75.646708 311.930671 222.332202 135.464353-69.417481-294.906962-153.170717 201.809885-64.254902L217.605029 592.860699l-37.332695 6.911882z m507.852657 80.083965l73.342747 80.339961-201.596556 66.985522L868.772572 1024l119.933951-61.481616-293.840314-135.592351 178.684948-56.831029-152.317398-96.425019-33.108768 6.186561z m0 0',
            symbolSize: 20,
            emphasis: {
              disabled: true
            },
            itemStyle: {
              color: function (params) {
                // console.log(params)
                if (params.data[1] > 580) {
                  return 'red'
                }
                return '#fff'
              }
            },
            lineStyle: {
              color: '#ddd',
              width: 1
            },
            data: data[5],
            // 设置警戒线
            markLine: {
              symbol: 'none',
              data: [
                // {
                //   name: '设备报警',
                //   yAxis: 105,
                //   ...opt.markLineStyle
                // },
                // {
                //   name: '维保报警',
                //   yAxis: 805,
                //   ...opt.markLineStyle,
                //   label: {
                //     position: 'start',
                //     formatter: '维保报警',
                //     padding: [0, 14, 0, 0]
                //   }
                // },
                {
                  name: '高温预警',
                  yAxis: 200,
                  label: {
                    show: false
                  },
                  lineStyle: {
                    color: 'red',
                    type: 'solid',
                    width: 3
                  }
                },
                {
                  name: '低温预警',
                  yAxis: 700,
                  label: {
                    show: false
                  },
                  lineStyle: {
                    color: '#0099b7',
                    type: 'solid',
                    width: 3
                  }
                },
                {
                  name: '月份间隔线',
                  xAxis: '2022-01-03 23:59:59',
                  lineStyle: {
                    color: '#fff',
                    type: 'solid',
                    width: 3
                  },
                  label: {
                    show: false
                  }
                },
                {
                  name: '月份间隔线2',
                  xAxis: '2022-01-06 23:59:59',
                  lineStyle: {
                    color: '#fff',
                    type: 'solid',
                    width: 3
                  },
                  label: {
                    show: false
                  }
                }
              ]
            }
          },
          /*
          散点图做报警状态
          {
            name: '设备报警状态标点',
            symbolSize: opt.size_alert,
            xAxisIndex: 0,
            yAxisIndex: 0,
            data: [
              ['2022-01-01 12:00:00', 105],
              ['2022-01-02 12:00:00', 105],
              ['2022-01-03 12:00:00', 105],
              ['2022-01-04 12:00:00', 105],
              ['2022-01-05 12:00:00', 105],
              ['2022-01-06 12:00:00', 105],
              ['2022-01-07 12:00:00', 105]
            ],
            type: 'scatter',
            itemStyle: {
              color: (params) => {
                const n = (Math.random() * 4).toFixed(0)
                return opt.color_alert[n]
              },
              opacity: 1
            },
            // 将虚线遮住
            z: 5
          },
          {
            name: '维保报警状态标点',
            symbolSize: opt.size_alert,
            xAxisIndex: 0,
            yAxisIndex: 0,
            data: [
              ['2022-01-01 12:00:00', 805],
              ['2022-01-02 12:00:00', 805],
              ['2022-01-03 12:00:00', 805],
              ['2022-01-04 12:00:00', 805],
              ['2022-01-05 12:00:00', 805],
              ['2022-01-06 12:00:00', 805],
              ['2022-01-07 12:00:00', 805]
            ],
            type: 'scatter',
            itemStyle: {
              color: (params) => {
                const n = (Math.random() * 4).toFixed(0)
                return opt.color_alert[n]
              },
              opacity: 1
            },
            z: 5
          },
          */
          // 散点图用作温度曲线的月份区分文本
          {
            name: '温度曲线图月份间隔',
            xAxisIndex: 0,
            yAxisIndex: 0,
            data: [['2022-01-02 12:00:00', 405]],
            type: 'scatter',
            // 不显示标点，只显示文本
            itemStyle: {
              color: 'transparent'
            },
            label: {
              show: true,
              formatter: '2022-01',
              fontWeight: 'normal',
              color: '#1acdfd',
              fontSize: 25
            },
            // 在曲线图之上显示，防止标签文本被遮挡
            z: 2
          },
          {
            name: '温度曲线图月份间隔202202',
            xAxisIndex: 0,
            yAxisIndex: 0,
            data: [['2022-01-05 12:00:00', 405]],
            type: 'scatter',
            // 不显示标点，只显示文本
            itemStyle: {
              color: 'transparent'
            },
            label: {
              show: true,
              formatter: '2022-02',
              fontWeight: 'normal',
              color: '#1acdfd',
              fontSize: 25
            },
            z: 2
          },
          {
            name: '温度曲线图月份间隔202203',
            xAxisIndex: 0,
            yAxisIndex: 0,
            data: [['2022-01-07 12:00:00', 405]],
            type: 'scatter',
            // 不显示标点，只显示文本
            itemStyle: {
              color: 'transparent'
            },
            label: {
              show: true,
              formatter: '2022-03',
              fontWeight: 'normal',
              color: '#1acdfd',
              fontSize: 25
            },
            z: 2
          },
          // 设备、维保 报警

          {
            name: 'alarm_0',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            barWidth: '100%',
            label: {
              show: true,
              formatter: '正常'
            },
            emphasis: {
              focus: 'series'
            },
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          {
            name: 'alarm_1',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            label: {
              show: true,
              formatter: '异常'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[1],
              borderColor: 'red',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          {
            name: 'alarm_2',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            label: {
              show: true,
              formatter: '正常'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          {
            name: 'alarm_3',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            label: {
              show: true,
              formatter: '异常'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[1],
              borderColor: 'red',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          {
            name: 'alarm_4',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            label: {
              show: true,
              formatter: '正常'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          {
            name: 'alarm_5',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            label: {
              show: true,
              formatter: '异常'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[1],
              borderColor: 'red',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          {
            name: 'alarm_6',
            type: 'bar',
            xAxisIndex: 3,
            yAxisIndex: 3,
            stack: 'alarmTotal',
            label: {
              show: true,
              formatter: '正常'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            data: [1, 1]
          },
          // 维保节拍
          {
            name: 'keepBeat0',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            stack: 'total',
            itemStyle: {
              color: '#b9a18a',
              borderColor: '#af826d',
              borderWidth: '2'
            },
            label: {
              show: true,
              formatter: '检修周期（1）'
            },
            emphasis: {
              focus: 'none'
            },
            barWidth: '100%',
            data: [null, null, 7]
          },
          {
            name: 'keepBeat1',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            stack: 'total',
            itemStyle: {
              color: '#cdd29b',
              borderColor: '#cda700',
              borderWidth: '2'
            },
            label: {
              show: true,
              formatter: '维保周期（1）'
            },
            emphasis: {
              focus: 'none'
            },
            barWidth: '90%',
            data: [null, 7]
          },
          {
            name: 'keepBeat',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            stack: 'total',
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            label: {
              show: true,
              formatter: '1-1-1'
            },
            emphasis: {
              focus: 'none'
            },
            barWidth: '80%',
            data: [1]
          },
          {
            name: 'keepBeat',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            stack: 'total',
            itemStyle: {
              color: opt.color[1],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            label: {
              show: true,
              formatter: '1-1-2'
            },
            emphasis: {
              focus: 'none'
            },
            data: [2 / 3]
          },
          {
            name: 'keepBeat',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            stack: 'total',
            label: {
              show: true,
              formatter: '1-1-3'
            },
            emphasis: {
              focus: 'none'
            },
            data: [2 / 3]
          },
          {
            name: 'keepBeat',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            itemStyle: {
              color: opt.color[1],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            stack: 'total',
            label: {
              show: true,
              formatter: '1-1-4'
            },
            emphasis: {
              focus: 'none'
            },
            data: [2 / 3]
          },
          {
            name: 'keepBeat',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: '#d9e7ce',
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            data: [2]
          },
          {
            name: 'keepBeat',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            stack: 'total',
            label: {
              show: true,
              formatter: '1-1-5'
            },
            emphasis: {
              focus: 'none'
            },
            itemStyle: {
              color: opt.color[0],
              borderColor: '#aebba5',
              borderWidth: '2'
            },
            data: [2]
          }
        ]
      }
      this.eChart.setOption(this.option)
    },
    getRandom(a, b) {
      return Number(Math.random() * (b - a) + a).toFixed(0)
    }
  }
}
</script>
<style lang="less" scoped>
@width_left: 200px;

#maintenance-screen.operationTable {
  width: 100%;
  height: 100%;
  padding: 24px;
  box-sizing: border-box;
  display: flex;
  flex-direction: initial;
  > div:first-child {
    width: @width_left;
    float: left;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    .top {
      background: #5d8eb7;
      width: 200px;
      margin-bottom: 5%;
      .el-row {
        font-size: 22px;
        font-weight: 600;
        color: white;
        padding: 5px 0 5px 30px;
        svg {
          margin: 0 6px;
          font-size: 28px;
        }
      }
    }
    .bottom {
      border: 2px dotted #fff;
      width: 200px;
      .el-row {
        display: flex;
        padding: 7px 0;
        .line {
          display: flex;
          justify-content: center;
          padding: 0 5px;
          align-items: center;
          > div {
            background: red;
            width: 70px;
            height: 3px;
          }
          .temperatureCurrent {
            background: black;
          }
          .temperatureLow {
            background: #0099b7;
          }
          .loadAlert {
            background: transparent;
            height: 0;
            border: 1px dashed #753225;
          }
          .loadCurrent {
            background: #9a0000;
          }
          .loadHeight {
            background: #cda700;
          }
          .loadLow {
            background: #006292;
          }
        }
        .text {
          font-size: 0.8rem;
        }
      }
    }
  }
  .operationTable-box {
    overflow: auto;
    flex: 1;
    margin-left: 24px;
    display: flex;
    #operationTable {
      flex: 1;
      height: 100%;
      width: 100%;
    }
  }
}

/deep/.tooltip-box {
  max-width: 240px;
}
/deep/div:has(div.hidden-box) {
  display: none;
}
</style>



